import React, { useState } from 'react';
import './App.css';
import AppBar from '@mui/material/AppBar';
import Button from '@mui/material/Button';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Typography from '@mui/material/Typography';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Badge from '@mui/material/Badge';

const MOVIE_CART = [
  {
    id: 1,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Fancy Product",
    rate: "$40.00 - $80.00",
    sale: false
  },
  {
    id: 2,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Special Item",
    rate: "$20.00 - $18.00",
    sale: true
  },
  {
    id: 3,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Sale Item",
    rate: "$50.00 - $25.00",
    sale: true
  },
  {
    id: 4,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Popular Item",
    rate: "$40.00",
    sale: false
  },
  {
    id: 5,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Sale Item",
    rate: "$50.00 - $25.00",
    sale: true
  },
  {
    id: 6,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Fancy Product",
    rate: "$40.00 - $80.00",
    sale: false
  },
  {
    id: 7,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Special Item",
    rate: "$20.00 - $18.00",
    sale: true
  },
  {
    id: 8,
    poster: "https://dummyimage.com/450x300/dee2e6/6c757d.jpg",
    name: "Popular Item",
    rate: "$40.00",
    sale: false
  }
];

function App() {
  const [cart, setCart] = useState([]);
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const toggleDrawer = (open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setDrawerOpen(open);
  };

  const handleMenuClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleAddToCart = (item) => {
    setCart((prevCart) => [...prevCart, item]);
  };

  const handleRemoveFromCart = (item) => {
    setCart((prevCart) => prevCart.filter(cartItem => cartItem.id !== item.id));
  };

  const isItemInCart = (item) => {
    return cart.some(cartItem => cartItem.id === item.id);
  };

  const menuItems = ['Home', 'About', 'Shop'];

  return (
    <div className="App">
      <AppBar position="static" color="default">
        <Toolbar>
          {isMobile && (
            <IconButton edge="start" color="inherit" aria-label="menu" onClick={toggleDrawer(true)}>
              <MenuIcon />
            </IconButton>
          )}
          <Typography variant="h6" style={{ flexGrow: 1 }}>
            Start Bootstrap
          </Typography>
          {!isMobile && (
            <div>
              {menuItems.slice(0, 2).map((item) => (
                <Button key={item} color="inherit">
                  {item}
                </Button>
              ))}
              <Button color="inherit" onClick={handleMenuClick}>
                Shop
              </Button>
              <Menu
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={handleMenuClose}
              >
                <MenuItem onClick={handleMenuClose}>All Products</MenuItem>
                <MenuItem onClick={handleMenuClose}>Popular Items</MenuItem>
                <MenuItem onClick={handleMenuClose}>New Arrivals</MenuItem>
              </Menu>
            </div>
          )}
          <IconButton color="inherit">
            <Badge badgeContent={cart.length} color="secondary">
              <ShoppingCartIcon />
            </Badge>
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer anchor="left" open={drawerOpen} onClose={toggleDrawer(false)}>
        <List>
          {menuItems.map((item) => (
            <ListItem button key={item} onClick={toggleDrawer(false)}>
              <ListItemText primary={item} />
            </ListItem>
          ))}
        </List>
      </Drawer>
      <div className="Topic">
        <h1>Shop in Style</h1>
        <p>With this shop homepage template</p>
      </div>
      <div className="cardlist">
        {MOVIE_CART.map((mv, index) => (
          <Dataset
            key={index}
            dataset={mv}
            onAddToCart={handleAddToCart}
            onRemoveFromCart={handleRemoveFromCart}
            isInCart={isItemInCart(mv)}
          />
        ))}
      </div>
      <div className="footer">
        <p>Copyright  Your Website 2023</p>
      </div>
    </div>
  );
}

export default App;

function Dataset({ dataset, onAddToCart, onRemoveFromCart, isInCart }) {
  return (
    <div className="card">
      {dataset.sale && <div className="sale-badge">sale</div>}
      <img className="poster" src={dataset.poster} alt={dataset.name} />
      <div className="spec">
        <h3>{dataset.name}</h3>
        <p>{dataset.rate}</p>
        <Button
          variant="outlined"
          className="button"
          onClick={() => isInCart ? onRemoveFromCart(dataset) : onAddToCart(dataset)}
        >
          {isInCart ? 'Remove from Cart' : 'Add to Cart'}
        </Button>
      </div>
    </div>
  );
}
